<template>
  <q-editor
    :autofocus="autofocus"
    :dense="dense"
    ref="descriptionEditorRef"
    v-model="internalModel"
    @paste="(e) => pastePlainText(e, $refs['descriptionEditorRef'])"
    :placeholder="placeholder"
    :rules="[(val) => (required ? !!val || '' : true)]"
    :height="`${(rows || 25) * 10}px`"
    :class="{ ['no-dropdown']: noDropDown, ['is-focused']: doneFocus }"
    @dropdown-before-show="doneFocus = true"
    @dropdown-before-hide="doneFocus = false"
    paragraph-tag="p"
    :toolbar="[
      [
        {
          // label: 'Font',
          icon: $q.iconSet.editor.font,
          list: 'no-icons',
          options: ['h1', 'h2', 'code', 'p'],
        },
        'bold',
        'italic',
        'underline',
        'strike',
        'unordered',
        'ordered',
        'link',
        {
          // label: 'Align',
          icon: $q.iconSet.editor.align,
          fixedLabel: true,
          list: 'only-icons',
          options: ['left', 'center', 'right', 'justify'],
        },
      ],
    ]"
  />
</template>

<script>
import { defineComponent } from 'vue';
import { Editor_Mixin } from 'src/mixins';

export default defineComponent({
  name: 'TextArea2',

  mixins: [Editor_Mixin],

  data() {
    return {
      internalModel: '',
      doneFocus: false,
    };
  },

  props: {
    autofocus: {
      type: Boolean,
      default: false,
    },
    required: {
      type: Boolean,
      default: false,
    },
    dense: {
      type: Boolean,
      default: true,
    },
    rows: {
      type: Number,
      default: 25,
    },
    placeholder: {
      type: String,
      default: 'Write something...',
    },
    noDropDown: {
      type: Boolean,
      default: true,
    },
  },
});
</script>
